// import axios from "axios";
import { useEffect, useState } from "react";
import { getSharedData, DOMAIN } from "./utils";

import "./App.css";

function App() {
    const [data, setData] = useState({ isLoaded: false });

    useEffect(() => {
        getSharedData().then((data) => {
            console.log(data);
            setData({ ...data, isLoaded: true });
        });
    }, []);

    return (
        <div className="App">
            <header className="App-header">
                <p>Hello!</p>
                <Firstname {...data} />
                <Message {...data} />
            </header>
        </div>
    );
}

const Firstname = () => {
    const [fname, setFname] = useState(null);

    // useEffect(() => {}, []);

    if (!fname) return <div>Loading ....</div>;
    return <div>I am {fname}</div>;
};

// const Firstname = ({ first_name }) => {
//     if (!first_name) return <div>Loading ....</div>;
//     return <div>I am {first_name}</div>;
// };

const Message = ({ isLoaded, message }) => {
    if (!isLoaded) return null;
    return <div>{message}</div>;
};

export default App;
